import React from "react"
import "normalize.css"
import "./illuminator.css"
import { graphql } from "gatsby"
import { Link } from 'gatsby-plugin-modal-routing'

export default ({ data }) => {
  return (
    <div style={{ color: "white" }}>
      {
        data.allIlluminatorYaml.nodes.map((node) => {
          return <Link to={ '/illuminator/' + node.name } asModal >
            <img src={'../../' + node.image}/>
          </Link>
        })
      }
    </div>
  )
}

export const query = graphql`
    query allIlluminator {
        allIlluminatorYaml {
            nodes {
                name
                image
            }
        }
    }
`
